@import url('https://fonts.googleapis.com/css?family=Nunito|PT+Sans');

* { 
  -webkit-box-sizing: border-box; 
          box-sizing: border-box;
}

body {
  background: -webkit-gradient(linear, left bottom, right top, from(#45007a), to(#4923bf) );
  background: -webkit-linear-gradient(left bottom, #45007a, #4923bf );
  background: -o-linear-gradient(left bottom, #45007a, #4923bf );
  background: linear-gradient(to right top, #45007a, #4923bf );
  font-family: 'PT-Sans', sans-serif;
  height: 700px;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
}

#app {
  background: -webkit-gradient(linear, left bottom, right top, from(#3d0e7b), to(#141a4a));
  background: -webkit-linear-gradient(left bottom, #3d0e7b, #141a4a);
  background: -o-linear-gradient(left bottom, #3d0e7b, #141a4a);
  background: linear-gradient(to right top, #3d0e7b, #141a4a);
  color: white;
  padding: 0 5% 0 5%;
  max-width: 500px;
  height: 550px;
  text-align: center;
  border-radius: 5%;
  border: groove;
  display: inline-block;
}

#main-header {
  margin-top: 0;
  padding-top: 5%;
  font-size: 2em;
  text-align: left;
  position: relative;
  left: 5%;
  color: #919fdd;
}

#score {
  color: #919fdd;
  position: absolute;
  top: 80%;
  left: 47%;
}

#talking-head {
  display: block;
  float: left;
  margin-top: 2%;
  width: 20%;
  height: 15%;
}


